import axios from 'axios'

var result = [];
const searchStore = {
  fetch: searchObj => {
    return new Promise((resolve, reject) => {
      axios.post('api/search', searchObj).then(data => {
        // data.data.forEach(item => result.push(item));
        result = data.data;
        console.log('Stored!')
        resolve(result);
      })
    })
  },
  get: () => result
}

export default searchStore



